import { useEffect, useState } from "react";

import "./App.css";
import axios from "axios";

import {
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
  TextField,
  Button,
  Typography,
  Link,
  Container,
} from "@material-ui/core";

import { createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";

const theme = createMuiTheme({
  typography: {
    fontFamily: [
      "Nunito",
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
    ].join(","),
  },
});

// UTILS
const toPercentage = (num) => {
  return `${(num * 100).toFixed(2)}%`;
};

const Hero = ({ userInfo }) => {
  return (
    <>
      <Typography variant="h2">{userInfo.title}</Typography>
      <Typography variant="p1">{userInfo.description}</Typography>
      <Typography variant="p2">
        <Link href={userInfo.link}>{userInfo.link}</Link>
      </Typography>
      <Typography>
        Page built using <Link href="https://withlaguna.com/">Laguna</Link>
      </Typography>
    </>
  );
};

const HoldingsTable = ({ holdings }) => {
  return (
    <>
      <Typography variant="h4">Current holdings</Typography>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Ticker</TableCell>
            <TableCell>Name</TableCell>
            <TableCell>Amount Held</TableCell>
            <TableCell>Total return</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {holdings.map((holding) => {
            if (holding.ticker_symbol.includes("CUR:")) {
              return <></>;
            }
            // Calculate return
            const percentageReturn =
              holding.institution_value / holding.cost_basis - 1;
            const amountHeld = holding.institution_value;
            return (
              <TableRow>
                <TableCell>{holding.ticker_symbol}</TableCell>
                <TableCell>{holding.name}</TableCell>
                <TableCell>${amountHeld.toFixed(2)}</TableCell>
                <TableCell>{toPercentage(percentageReturn)}</TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </>
  );
};

const SubscribeUpdateForm = ({ userInfo }) => {
  const [submitted, setSubmitted] = useState(false);
  const [value, setValue] = useState("");
  const [error, setError] = useState(false);

  const handleSubmit = (e) => {
    setError(false);
    setSubmitted(true);
    axios
      .post("")
      .then((res) => {})
      .catch(() => {
        setSubmitted(false);
        setError(true);
      });
  };

  return (
    <form>
      <TextField
        id="phone"
        lable="Phone number"
        variant="filled"
        value={value}
        onChange={(e) => setValue(e.value)}
        defaultValue="555-555-5555"
        helperText="By submitting, you agree to data usage terms"
        error={error}
      />
      <Button variant="contained" onClick={handleSubmit}>
        Get notified
      </Button>
    </form>
  );
};

const TradesTable = ({ trades }) => {
  // Select only the three most recent trades
  const recent_trades = trades.slice(0, 3);

  // CTA to sign up on bottom
  return (
    <>
      <Typography variant="h4">3 most recent trades</Typography>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Date</TableCell>
            <TableCell>Ticker</TableCell>
            <TableCell>Quantity</TableCell>
            <TableCell>Price</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {recent_trades.map((trade) => {
            return (
              <TableRow>
                <TableCell>{trade.trade_date}</TableCell>
                <TableCell>{trade.ticker}</TableCell>
                <TableCell>{trade.quantity}</TableCell>
                <TableCell>${trade.price}</TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </>
  );
};

function CreateYourOwnPage() {
  return (
    <>
      <Typography>Interested in having your own page?</Typography>
      <Button href="https://withlaguna.com">Join Laguna</Button>
    </>
  );
}

function App() {
  useEffect(() => {
    fetchPortfolio();
  }, []);

  const [holdings, setHoldings] = useState([]);
  const [trades, setTrades] = useState([]);
  const [userInfo, setUserInfo] = useState([]);

  const fetchPortfolio = () => {
    axios.get("https://api.withlaguna.com/stonks/holdings").then((res) => {
      setHoldings(res.data.holdings);
    });
    axios.get("https://api.withlaguna.com/stonks/trades").then((res) => {
      setTrades(res.data.trades);
    });
    axios.get("https://api.withlaguna.com/stonks/userinfo").then((res) => {
      // setUserInfo(res.data.user);

      // override userinfo
      setUserInfo({
        title: "Rob's stonks",
        description: "Investing with minimal DD in almost pure tech",
        link: "https://robertolsthoorn.com",
      });
    });
  };

  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <Container maxWidth="md">
          <Hero userInfo={userInfo} />
          <TradesTable trades={trades} />
          <SubscribeUpdateForm userInfo={userInfo} />
          <HoldingsTable holdings={holdings} />
          <CreateYourOwnPage />
        </Container>
      </div>
    </ThemeProvider>
  );
}

export default App;
